@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}


.mainNavbar{
    position: absolute;
    left: 0;
    width: 70px;
    height: 100vh;
    background-color: black;
}

.mainNavbarLogo{
    width: 100%;
    padding: 5px;
}

.mainNavbarLogo img{
    cursor: pointer;
    width: 100%;
    object-fit: contain;
}

.mainNavbarItems{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px;
}

.mainHome {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
}

.mainHomeContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.mainHomeContent code {
    font-size: 1.2rem;
    margin-top: 5px;
}

.mainHomeContent code b {
    font-family: 'Source Code Pro';
    background-color: rgba(90, 90, 90, 0.527);
    padding: 5px 10px;
}

.mainHomeContent code strong {
    background: linear-gradient(90deg, rgba(0,195,195,1) 0%, rgba(246,0,228,1) 100%);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.mainHomeContentTitle {
    font-size: 2rem;
    font-family: 'Source Code Pro';
    background-color: rgba(90, 90, 90, 0.527);
    padding: 10px 20px;
}

.mainHomeContent > code {
    margin-bottom: 10px;
}

.mainHomeContentsBox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    grid-gap: 2px;
    gap: 2px;
    border: solid 1px #fff;
    border-radius: 5px;
}

.mainHomeContentsBox span {
    padding: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 5px;
    gap: 5px;
    cursor: pointer;
}

.mainHomeContentsBox span:nth-child(even) {
    height: 100%;
    padding: 0;
    width: 1px;
    border: solid 1px #fff;
    border-left: 0;
}

.mainHomeContentsBox a {
    text-decoration: none;
    color: #fff;
}

.mainProjects{
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.mainProjectsItem{
    position: relative;
    padding-top: 100%;
    width: 100%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.mainProjectsSubItem{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    overflow: hidden;
}

.mainProjectsSubItemFront, .mainProjectsSubItemBack{
    width: 100%;
    height: 100%;
    position: absolute;
    color: #fff;
}

.mainProjectsSubItemBack::before{
    background: linear-gradient(to top, black, transparent);
}

.mainProjectsSubItemBack::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.514);
}

.mainProjectsSubItemBack::after{
    content: "";
    position: absolute;
    color: transparent;
    font-weight: 900;
    font-size: 3rem;
    -webkit-text-stroke: rgba(255, 255, 255, 0.404) 1px;
    left: 0;
    bottom: 0;
}

#project::after{
    content: "PROJETO";
}

#research::after{
    content: "PESQUISA";
}

.mainProjectsSubItemBack img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainProjectsSubItemFront{
    background-color: black;
    transition: .2s ease-in-out;
    /* transform: translateY(-100%); */
    color: #fff;
}

.mainProjectsSubItem:hover .mainProjectsSubItemFront{
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

.mainProjectsSubItemFront h3{
    font-weight: 900;
    font-size: 5rem;
    margin: 0 50px;
    color: transparent;
    -webkit-text-stroke: rgba(255, 255, 255, 0.336) 1px;
}

.mainProjectsItemDesc{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: -40px;
    text-align: center;
    padding: 20px;
    /* z-index: 100; */
}

.mainProjectsItemDesc h2{
    font-weight: 300;
}

.mainProjectsItemDesc p{
    font-weight: 200;
}

.mainProjectsItemDesc button{
    background-color: transparent;
    color: #fff;
    border-style: none;
    cursor: pointer;
}

@media screen and (max-width: 950px) {
    .mainProjects {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (max-width: 650px) {
    .mainProjects {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 500px) {
    .mainProjects {
        grid-template-columns: repeat(1, 1fr);
    }
}

.mainAbout {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: calc(100vh/4);
    width: 100%;
    grid-gap: 50px;
    gap: 50px;
}

.mainAboutSeparator hr {
    width: 100%;
    color: rgba(90, 90, 90, 0.185);;

}

.mainAboutSeparator {
    width: 100%;
    max-width: 800px;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 400;
    font-size: 1.4rem;
}

.mainAboutUpper {
    display: flex;
    flex-direction: column;
}

.mainAbout h1 {
    color: #fff;
}

.mainAboutFooter {
    padding: 10px 0 5px 0;
    max-width: 800px;
    width: 100%;
    background-color: rgba(90, 90, 90, 0.185);;
    display: flex;
    grid-gap: 40px;
    gap: 40px;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    color: rgba(255, 255, 255, 0.575);
}

.mainAboutContent {
    display: flex;
    flex-direction: row;
    max-width: 800px;
    width: 100%;
}

.mainAboutContent > div {
    flex: 1 1;
    background-color: rgba(90, 90, 90, 0.527);
}

.mainAboutContentImg {
    position: relative;
}

.mainAboutContentImg img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.mainAboutContentInfos {
    padding: 30px;
    color: #fff;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    gap: 30px;
}

.mainAboutContentInfos h2 {
    font-weight: 400;
    margin: 0;
    margin-bottom: 5px;
}

.mainAboutContentInfosSection {
    display: flex;
    flex-direction: column;
}

.mainAboutWorks {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1000px;
    width: 100%;
    color: #fff;
}

.mainAboutWorksSection {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.mainAboutWorksSection .mainAboutWorksSectionItem:nth-child(1) {
    text-align: end;
}

.mainAboutWorksSectionItem {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    padding: 10px 40px;
    font-weight: 300;
    padding-bottom: 50px;
    grid-gap: 5px;
    gap: 5px;
}

.mainAboutWorksSection > span {
    position: relative;
    width: 1px;
    height: 100%;
    border: solid 1px rgba(90, 90, 90, 0.527);;
}

.mainAboutWorksSection > span::before {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 7px;
    background-color: #fff;
    top: -3px;
    left: -3px;
}

.mainAboutSkills {
    width: 100%;
    max-width: 800px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 0 50px 200px 0;
    color: #fff;
    grid-gap: 40px;
    gap: 40px;
}

.mainAboutSkills span {
    padding: 5px 20px;
    border: solid 1px #fff;
    border-radius: 20px;
}

.mainAboutColors {
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mainAboutColorsIcon {
    width: 7px;
    height: 7px;
    border-radius: 7px;
    margin: 0 10px 0 25px;
}

.mainAboutColors .avancado {
    background-color: green;
}

.mainAboutColors .intermediario {
    background-color: yellow;
}

.mainAboutColors .iniciante {
    background-color: red;
}

.mainAboutSkills .avancado {
    border-color: rgba(0, 128, 0, 0.329);
}

.mainAboutSkills .intermediario {
    border-color: rgba(255, 255, 0, 0.322);
}

.mainAboutSkills .iniciante {
    border-color: rgba(255, 0, 0, 0.329);
}
.App{
    width: 100vw;
    height: 100vh;
    background-color: black;
    display: flex;
    padding-left: 70px;
    overflow-y: auto;
}

.icon{
    color: #fff;
    opacity: .5;
    cursor: pointer;
}

.icon:hover{
    opacity: 1;
}
